import './home.scss';
import Carousels from '../Carousels';
import cafe from '../../assets/coffeeshop.jpg';

function Home() {
  return (
    <div className="home">
        <Carousels />
      <div className="presentation">
        <div className="presentation__img">
          <img src={cafe} alt="caf" />
        </div>
        <div className="home__presentation">
          <p>CHIBI CAFE est un coffee shop / salon de th situ en plein coeur de Paris.</p>
            
          <p>  Nous vous proposons des cafs et ths de spcialit, des dclinaisons de ptisseries et des boissons colores.
            Une picerie fine proposant un large choix de produits sucrs.</p>
          <div className="presentation__link">
            <a className="store-nav__link"  href="/menu">Voir le menu</a>
            <a className="store-nav__link"  href="/boutique">Voir la boutique</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;